import React from "react";
import Layout from "../components/layout";
import Tabs from "../components/tabs";
import Member from "../components/member";
import Button from "../components/button";
import { graphql } from "gatsby"

const allData = {
  fund: { people: 30, inputs: 300, outputs: 45},
  members: [
    { name: "Amina", phone: 173739279, shares: 31, loans: 4 },
    { name: "Fara", phone: 24627835, shares: 15, loans: 34 },
    { name: "Ladi", phone: 18394026, shares: 25, loans: 4 },
    { name: "Hannah", phone: 17363898, shares: 13, loans: 3 }
  ],
  meetings: [
    { people: "6", date: "01-02-19", ins: "10", outs: "3" },
    { people: "10", date: "03-04-18", ins: "100", outs: "80" },
    { people: "15", date: "29-05-17", ins: "145", outs: "30" },
    { people: "21", date: "02-06-16", ins: "10", outs: "13" },
    { people: "9", date: "015-07-15", ins: "30", outs: "33" },
    { people: "18", date: "03-08-14", ins: "10", outs: "53" },
    { people: "20", date: "15-10-13", ins: "70", outs: "3"}
  ]
};

const AddMember = props => (
  <div className="level" style={{marginBottom: `10px`}}>
    <div className="level-litem">
      <Button onClick= {props.onClick} class="fas fa-lg fa-user-alt has-text-info" text="Kara Mamobbi" />
    </div>
  </div>
)

class MembersList extends React.Component {

  constructor(props) {
    super(props);
    this.addMember = this.addMember.bind(this);
    this.state = {
      members: props.members
    };
    this.numbers = ["754238536", "05489806", "654593536", "15463536", "95464536"]
    this.names = ["Assibi", "Cof", "Coffi", "Coffie", "Colee", "Coley", "Colie", "Dura", "Durah", "Durrah", "Fara", "Fari", "Farih", "Farra", "Farry", "Gaddo", "Gambo", "Jummai", "Ladi"];
    this.pics = props.pics;
  }
  addMember() {
    console.log("button pressed");
    this.setState({
      members: [
        {name: this.names[Math.floor(Math.random() * (this.names.length - 1))], phone: this.numbers[Math.floor(Math.random() * (this.numbers.length - 1))], shares: Math.floor(Math.random() * 50) + 1, loans: Math.floor(Math.random() * 20) + 1},
        ...this.state.members
      ]
    })
  }

  render() {
    return (
      <div>
        <AddMember onClick={this.addMember}/>
        {this.state.members.map((member, index) => (
          <Member key={index} img={this.pics[Math.floor(Math.random() * (this.pics.length - 1))]} name={member.name} phone={member.phone} shares={member.shares} loans={member.loans} />
        ))}
      </div>
   )
  }
}

export default ({data}) => {
  return(
    <Layout>
      <Tabs />
      <MembersList members={allData.members} pics={data.pics.edges}/>
    </Layout>
  );
}
export const query = graphql`
  query {
    pics: allFile(filter: {relativeDirectory: {eq: "members"}}) {
    edges {
      node {
        childImageSharp {
          # Specify the image processing specifications right in the query.
          # Makes it trivial to update as your page's design changes.
          fixed(width: 64, height: 64) {
            ...GatsbyImageSharpFixed
          }
        }
      }
   }
  }
}
`
